<template>
  <div class="page-content config-page">
    <!--tab-->
    <div style="position: relative">
      <el-row>
        <el-col>
          <el-button
            type="primary"
            @click="handleAdd"
          >
            
          </el-button>
          <!-- <el-button type="primary" @click="handleExport"></el-button> -->
          <el-button
            type="danger"
            @click="handleDelete"
          >
            
          </el-button>

          <el-form
            :inline="true"
            style="float:right"
          >
            <el-form-item style="margin-bottom:0">
              <el-input
                v-model="keyword"
                placeholder=""
              />
            </el-form-item>
            <el-form-item style="margin-bottom:0">
              <el-button
                type="primary"
                @click="handleSearch"
              >
                
              </el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
    <!---->
    <div style="margin-top: 20px">
      <el-table
        :data="userList"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          :selectable="onSelectable"
          type="selection"
          width="55"
        />
        <el-table-column
          v-for="item in tableColumn"
          :key="item.prop"
          :prop="item.prop"
          :label="item.label"
          :width="'auto'"
        />
        <el-table-column
          width="180"
          label=""
        >
          <template slot-scope="scope">
            <el-button
              v-if="scope.row.name!='admin'"
              type="text"
              size="small"
              @click="handleEdit(scope.row)"
            >
              
            </el-button>
            <el-button
              v-if="scope.row.name!='admin'"
              type="text"
              size="small"
              @click="handleDelete(scope.row)"
            >
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="page-line">
        <el-pagination
          :total="total"
          :page-size="pageSize"
          :current-page.sync="page"
          small
          layout="prev, pager, next"
          @current-change="onPageChange"
        />
      </div>
    </div>
    <!---->
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
    >
      <el-form
        :model="formdata"
        :rules="rules"
        label-width="100px"
      >
        <el-form-item
          label=""
          prop="name"
        >
          <el-input
            v-model="formdata.name"
            :disabled="isEdit"
            autocomplete="off"
          />
        </el-form-item>
        <el-form-item
          v-if="!isEdit"
          label=""
          prop="password"
        >
          <el-input
            v-model="formdata.password"
            type="password"
            autocomplete="off"
          />
        </el-form-item>
        <el-form-item
          label=""
          prop="mobile"
        >
          <el-input
            v-model="formdata.mobile"
            :disabled="isEdit"
          />
        </el-form-item>
        <el-form-item
          :model="formdata"
          label=""
          prop="email"
        >
          <el-input v-model="formdata.email" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formdata.department" />
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="formdata.job" />
        </el-form-item>
        <el-form-item
          label=""
          prop="role_id"
        >
          <el-select v-model="formdata.role_id">
            <el-option
              v-for="item in roleList"
              :key="item.role_id"
              :label="item.role_name"
              :value="item.role_id"
            />
          </el-select>
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button
          type="primary"
          @click="formSubmit"
        > </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import API from "../../service/index.js"

export default {
  data() {
    return {
      tableColumn: [
        { prop: "id", label: "id" },
        { prop: "name", label: "" },
        { prop: "role_name", label: "" },
        { prop: "mobile", label: "" },
        { prop: "created_at", label: "" }
      ],
      totalItem: 0,
      currentPage: 1,
      isEdit: false,
      dialogVisible: false,
      selectedIds: [],
      userList: [],
      roleList: [],
      menuList: [],
      formdata: {},
      total: 0,
      pageSize: 20,
      page: 1,
      keyword: "",
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        password: [{ required: true, message: "", trigger: "blur" }],
        mobile: [{ required: true, message: "", trigger: "blur" }],
        email: [{ required: true, message: "", trigger: "blur" }],
        role_id: [{ required: true, message: "", trigger: "blur" }]
      }
    }
  },
  computed: {
    dialogTitle() {
      return this.isEdit ? "" : ""
    }
  },
  mounted() {
    this.getUserList()
    this.getRoleList()
    this.getMenuList()
  },
  methods: {
    handleAdd() {
      this.isEdit = false
      this.dialogVisible = true
      this.formdata = {}
    },
    handleEdit(row) {
      this.isEdit = true
      this.dialogVisible = true
      this.formdata = Object.assign({}, row)
      this.formdata.user_id = row.id
    },
    handleDelete(row) {
      if (!row.id && this.selectedIds.length === 0) {
        this.$message("")
        return
      }
      let ids = row.id ? [row.id] : this.selectedIds.map(item => item.id)
      this.$confirm("", "").then(() => {
        API.deleteUser(JSON.stringify(ids)).then(res => {
          this.$message("")
          this.getUserList()
        })
      })
    },
    handleExport() { },
    handleSelectionChange(val) {
      this.selectedIds = val
    },
    onSelectable(row, index) {
      if (row.name == "admin") return false
      return true
    },
    onPageChange(page) {
      this.getUserList()
    },
    formSubmit() {
      API[this.isEdit ? "editUser" : "addUser"](this.formdata).then(res => {
        this.$message(this.isEdit ? "" : "")
        this.dialogVisible = false
        this.getUserList()
      })
    },
    handleSearch() {
      this.page = 1
      this.getUserList()
    },
    getUserList() {
      API.userList({
        keyword: this.keyword,
        page: this.page,
        limit: this.pageSize
      }).then(res => {
        this.userList = res.data.result.data
        this.total = res.data.result.total
      })
    },
    getRoleList() {
      API.roleList().then(res => {
        this.roleList = res.data.result
      })
    },
    getMenuList() {
      API.menuList().then(res => {
        this.menuList = res.data.result
      })
    },
    getUserDetail(id) {
      API.getUser(id).then(res => {
        this.formdata = res.data.result
      })
    }
  }
}
</script>



