<template>
  <div class="page-content">
    <!-- tab -->
    <div class="filter">          
      <span></span>
      <el-button
        type="primary" 
        @click="showConfig('add')"></el-button>
    </div>
    <!--  -->
    <div style="margin-top: 60px">
      <el-table
        :data="list"
        border>
        <el-table-column
          :index="indexMethod"
          type="index"
          label=""
          width="80"/>
        <el-table-column
          prop="name"
          label=""/>
        <el-table-column
          prop="time"
          label=""/>
        <el-table-column
          prop="state"
          label="">
          <template slot-scope="scope">
            {{ scope.row.state ? '':'' }}
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small" 
              @click="showConfig('modify', scope.row)"></el-button>
            <el-button
              type="text"
              size="small" 
              @click="handeStateClick(scope.row.state)">
              {{ scope.row.state ? '':'' }}
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="handeDeleteClick(scope.row.state)"></el-button>
          </template>
        </el-table-column>
      </el-table>

    </div>
    <div class="block">
      <el-pagination
        :total="+pages.total"
        :page-size="+pages.limit"
        :current-page="+pages.page"
        background
        layout="prev, pager, next"
        @current-change="handPageChange"/>
    </div>
    <!-- config -->
    <Config :config="config"/>
  </div>
</template>
<style lang="less" scoped>
.filter{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-bottom: 10px;
  border-bottom: 2px solid #ccc;
}
.block {
  text-align: right;
  margin: 30px 0;
}
</style>
<script>
import Config from './components/config.vue'
export default {
  components: {
    Config
  },
  data() {
    return {
      pages: {
        page: '1',
        limit: '10',
        total: '40'
      },
      list: [{
        date: '01',
        name: '',
        upmode: "",
        time: '2019-01-09 15:21:21',
        state: 0,
        address: ' 1518 '
      }, {
        date: '02',
        name: '',
        upmode: "wifi",
        time: '2019-01-09 15:21:21',
        state: 0,
        address: ' 1517 '
      }, {
        date: '03',
        name: '',
        upmode: "",
        time: '2019-01-09 15:21:21',
        state: 0,
        address: ' 1519 '
      }, {
        date: '04',
        name: '',
        upmode: "",
        time: '2019-02-09 15:21:21',
        state: 1,
        address: ' 1516 '
      }, {
        date: '05',
        name: '',
        upmode: "",
        time: '2019-03-09 15:21:21',
        state: 1,
        address: ' 1516 '
      }],
      config : {}
    }
  },
  methods: {
    getList() {
    },
    handeStateClick(state) {
      let type = ''
      if(state){
        type = ''
      }
      this.$confirm(`${type}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
      }).catch(() => {
      })
    },
    handeDeleteClick() {
      this.$confirm(``, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
      }).catch(() => {
      })
    },
    handPageChange(val) {
      this.pages.page = val
      this.getList()
    },
    indexMethod(index) {
      return index + 1
    },
    showConfig(type, item) {
      if(type == 'add'){
        this.config ={
          type: type,
          show: true,
          name: ''
        }
      } else {
        this.config = {
          type: type,
          show: true,
          ...item
        }
      }
    }
  }
}
</script>



