import {
  FamilyImgList,
  Strategy,
  StrategyUuid,
  StrategyConfig,
  CmData,
  MallButtonConfig,
  MallOrder,
  SceneAdmin,
  SightManage,
  StartMode
} from '../../pages/index.js'

const configManage = [
  {//
    path: 'familyImgList',
    name: 'familyImgList',
    component: FamilyImgList
  },
  {//
    path: 'Strategy',
    name: 'Strategy',
    component: Strategy
  },
  {//
    path: 'StrategyUuid',
    name: 'StrategyUuid',
    component: StrategyUuid
  },
  {//
    path: 'StrategyConfig',
    name: 'StrategyConfig',
    component: StrategyConfig
  },
  {//
    path: 'CmData',
    name: 'CmData',
    component: CmData
  },
  //
  {//
    path: 'mallButtonConfig',
    component: MallButtonConfig
  },
  {
    path: 'mallOrder',
    component: MallOrder
  },
  // 
  {
    path: 'sceneAdmin',
    component: SceneAdmin
  },
  // // 
  // {
  //   path: 'sightManage',
  //   component: SightManage
  // },
  { // startMode
    path: 'startMode',
    component: StartMode
  }
]

export default configManage



