export function formatValue(key, val) {
  if (key == 'force') {
    return {
      0: '',
      1: ''
    }[val]
  }
  if (key == 'release_type') {
    return {
      0: '',
      1: '',
      2: ''
    }[val]
  }
  if (key == 'status') {
    return {
      0: '',
      1: ''
    }[val]
  }
  return ''
}



