<template>
  <div class="page-content">
    <!-- tab -->
    <div class="filter">
      <span></span>
      <el-button
        type="primary"
        @click="showConfig('add')"></el-button>
    </div>
    <!--  -->
    <div style="margin-top: 60px">
      <el-table
        :data="list"
        border>
        <el-table-column
          :index="indexMethod"
          type="index"
          label=""
          width="80"/>
        <el-table-column
          prop="scene_name"
          label=""/>
        <el-table-column
          prop="mode_name"
          label=""/>
        <el-table-column
          prop="created_at"
          label=""/>
        <el-table-column
          prop="enable"
          label="">
          <template slot-scope="scope">
            {{ scope.row.enable ? '':'' }}
          </template>
        </el-table-column>
        <el-table-column
          label="">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="showConfig('look', scope.row)"></el-button>
            <el-button
              type="text"
              size="small"
              @click="showConfig('modify', scope.row)"></el-button>
            <el-button
              type="text"
              size="small"
              @click="handeEnableClick(scope.$index,scope.row.enable)">
              {{ scope.row.enable ? '':'' }}
            </el-button>
            <el-button
              type="text"
              size="small"
              @click="handeDeleteClick(scope.row.scene_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>

    </div>
    <!-- <div class="block">
      <el-pagination
        :total="+pages.total"
        :page-size="+pages.limit"
        :current-page="+pages.page"
        background
        layout="prev, pager, next"
        @current-change="handPageChange"/>
    </div> -->
    <!-- config -->
    <Config 
      ref="configDialog" 
      @refresh="refresh"/>
  </div>
</template>
<style lang="less" scoped>
.filter{
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  padding-bottom: 10px;
  border-bottom: 2px solid #ccc;
}
.block {
  text-align: right;
  margin: 30px 0;
}
</style>
<script>
import { PREFIX, deepClone } from "../../lib/util"
import Config from './components/config.vue'
export default {
  components: {
    Config
  },
  data() {
    return {
      list: []
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    getList() {
      this.$http
        .post(PREFIX + 'iotscene/lists', {})
        .then(res => {
          this.list = res.data.result.list
        })
        .catch(res => {
          if (res && res.msg) {
            this.$message.error(res.msg)
          } else {
            this.$message.error(res)
          }
        })
    },
    submitEdit(param) {
      this.$http
        .post(PREFIX + 'iotscene/save', param)
        .then(res => {
          this.$message.info('')
          this.getList()
        })
        .catch(res => {
          if (res && res.msg) {
            this.$message.error(res.msg)
          } else {
            this.$message.error(res)
          }
        })
    },
    delete(cId) {
      this.$http
        .post(PREFIX + 'iotscene/del', {
          scene_id: cId
        })
        .then(res => {
          this.$message.info('')
        })
        .catch(res => {
          if (res && res.msg) {
            this.$message.error(res.msg)
          } else {
            this.$message.error(res)
          }
        })
    },
    dealUpdateParam(index, enable) {
      let param = deepClone(this.list[index])
      param.enable = +!+enable
      param.list_pic.normal = param.list_pic.normal_object
      delete param['list_pic']['normal_object']
      param.detail_pic = param.detail_pic_object
      delete param['detail_pic_object']
      return param
    },
    handeEnableClick(index,enable) {
      let type = ''
      if(enable){
        type = ''
      }
      this.$confirm(`${type}`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        console.log(enable)
        let param = this.dealUpdateParam(index, enable)
        this.submitEdit(param)
      }).catch(() => {
        this.$message.info('')
      })
    },
    handeDeleteClick(cId) {
      this.$confirm(``, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.delete(cId)
      }).catch(() => {
        this.$message.info('')
      })
    },
    handPageChange(val) {
      this.pages.page = val
      this.getList()
    },
    indexMethod(index) {
      return index + 1
    },
    showConfig(type, item) {
      let defaultConfig = {
        type: type,
        show: true,
        scene_name: '',
        list_pic: {
          normal: ''
        },
        detail_pic: '',
        mode_id: '',
        enable: 1,
        content: {
          list: []
        }
      }
      if(type == 'add'){
        this.$refs.configDialog.config = defaultConfig
      } else {
        this.$refs.configDialog.config = Object.assign({}, defaultConfig, item)
      }
      // 
      this.$refs.configDialog.dealList()
    },
    //  
    refresh(val) {
      if(val) this.getList()
    }
  }
}
</script>



